
<template>
  <div class="home">
    <!-- 中间词云-->
    <div class="news_map">
       <wordCloud />

    </div>
    
    <!-- 头部专题切换 -->
    <div class="home_header">
      <el-button type="primary" class="all country item" @click="all()">全国版</el-button>
      <!-- <router-link to="/home" class="all item">全国版</router-link> -->
      <div class="top_bar">
        <div class="center_content">
          <div class="topleft">
            <el-button size="medium" class="subject item" @click="change(1)"
              >学科建设</el-button
            >
            <el-button class="enroll item" @click="change(2)"
              >招生就业</el-button
            >
            <el-button class="teach item" @click="change(3)"
              >师风师德</el-button
            >
          </div>
          
          <el-button class="all" @click="change(9)">热点舆情总览</el-button>
          <div class="topright">
            <el-button class="school_construction item" @click="change(4)"
              >校园基建</el-button
            >
            <el-button class="bad item" @click="change(5)">学术不端</el-button>
            <el-button class="epidemic_health item" @click="change(6)"
              >疫情专题</el-button
            >
          </div>
        </div>
      </div>
    </div>

    <div class="home_content">
      <!-- 左侧新闻概览展示 -->
      <div class="content_left">
        <div class="news_index">
          <cquptNews ref="sonCqupt" />
        </div>
      </div>
      <!-- 总数概览 -->
      <div class="content_center">
        <div class="news_num">
          <newsNum ref = "sonNums"/>
        </div>
      </div>

      <div class="contetn_right">
        <!-- 警报 -->
        <div class="warn">
          <div class="warnImg" v-if="!isShow" @click="toReport()">
            <div>你有紧急舆情需处理 ！！！</div>
            <img src="../assets/img/warn.png" alt="">
            <div class="content" v-model="text">{{text}}</div>
          </div>
          <trend v-else="isShow"/>
          <!-- <trend /> -->
        </div>

        <!-- 情感柱状 -->
        <div class="text_analyse">
          <emotion />
        </div>
      </div>
    </div>
    <div class="change_page">
      <changePage />
    </div>
  </div>
</template>

<script>
import topBar from "@/components/navigation/TopBar";
import singleMap from "@/components/content/all/Map.vue";
import trend from "@/components/content/all/Trend.vue";
import cquptNews from "@/components/content/all/cquptHot3.vue";
import emotion from "@/components/content/all/cquptEmotion.vue";
import newsNum from "@/components/content/all/cquptNum";
import changePage from "@/components/navigation/AllBar";

import wordCloud from "@/components/content/all/wordCloud.vue";

export default {
  data() {
    return {
      text: "",
      isShow: true,
      flag: "all",
      input: "",
      weibo_id:'',
      
    };
  },
  components: {
    topBar,
    singleMap,
    trend,
    cquptNews,
    emotion,
    newsNum,
    changePage,
    wordCloud
  },
  mounted() {
    this.getState()
    this.cqupt()
  },
  methods: {
    all () {
      this.$router.push('/home')
    },
   cqupt () {
     this.$url
        .get("news/cquptHot3/")
        .then((res) => {
          let screenDataCQ = res.data;
          this.allData = res.data;
          this.$store.commit("setScreenDataCQ", screenDataCQ);
          this.$refs.sonCqupt.onClick();
        })
        .catch((err) => {
          // console.log(err);
        });
   },
    change(e) {
      let keyWord;
      switch (e) {
        case 9:
          keyWord = 9;
          break;
        case 1:
          keyWord = "学科建设";
          break;
        case 2:
          keyWord = "招生就业";
          break;
        case 3:
          keyWord = "师风师德";
          break;
        case 4:
          keyWord = "校园基建";
          break;
        case 5:
          keyWord = "学术不端";
          break;
        case 6:
          keyWord = "疫情专题";
          break;
        default:
          break;
      }

        let param;
        if (keyWord == 9) {
          param = "news/cquptHot3/";
        } else {
          param = "news/cquptHot3/?aspect=" + keyWord;
        }
        this.$url
          .get(param)
          .then((res) => {
            let screenDataCQ = res.data;
            this.allData = res.data;
              this.$store.commit("setScreenDataCQ", screenDataCQ);
              this.$refs.sonCqupt.onClick();
            })
            .catch((err) => {
              // console.log(err);
            });
      
    },
    //预警请求
    getState() {
      function isEmpty(obj) {
        for (var o in obj) {
            // console.log(o, 777)
            return false;
        }
        return true;
      }
    
      this.$url.get('news/cquptHot20/?label=1')
      .then(res => {
        if(res.data.code === 200) {
          // console.log(res.data)
         if(isEmpty(res.data.data) == false){
            this.isShow = false
            let newData = res.data.data[0]
            // console.log(newData)
            this.weibo_id = res.data.data[0].weibo_id
            this.text = res.data.data[0].raw_text
            let author = {
              author_id:newData.author_id,
              author_id_id:newData.author_id_id,
              author_avatar:newData.author_avatar,
              raw_text:newData.raw_text,
              author_gender:newData.author_gender,
              weibo_id:newData.weibo_id,
              f:newData.gender.f,
              m:newData.gender.m,
              hot_comments:newData.hot_comments,
              history_weibo:newData.history_weibo,
              comment_word_cloud:newData.comment_word_cloud,
              neg:newData.comment_emotion_rate.neg,
              other:newData.comment_emotion_rate.other,
            }
            this.$store.commit('setNickName',author);
            this.$store.commit('setAuthor',author);
            this.$store.commit('setAvatar',author);
            this.$store.commit('setText',author);
            this.$store.commit('setGender',author);
            this.$store.commit('setHistoryWeibo',author);
            this.$store.commit('setGender',author);
            this.$store.commit('setCommit',author);
            this.$store.commit('setCommentCloud',author);
            this.$store.commit('setCommentEmotion',author)
              this.$store.commit('setWebId',author);
            // console.log('预警存的',author)
         }
        }
      })
      .catch(err => {
        // console.log(err)
      })
      
    },
    //预警处理后跳转
    toReport () {
      let param = this.weibo_id
      this.$url.get('news/HandleEarlyWarning/',{
        params:{
          weibo_id:param
          }
        })
      .then(res => {
        this.$router.push('/manage/report')
      })
      .catch(err => {
        // console.log(err)
      })
    },

  },
  
};
</script>

<style scoped lang="less">
.top_bar {
  display: flex;
  justify-content: space-around;
  height: 80px;
  padding-top: 40px;
  background: url("../assets/screen/tittle.png") no-repeat;
  background-size: 100% 100%;
  z-index: -10;
}
.all {
  float: right;
  color:aqua;
 
  text-decoration: none;
}
.country{
 margin-bottom: -200px;
}


.center_content {
  width: 94%;
  display: flex;
  justify-content: space-between;
  font-size: 22px;
  font-size: 600;
  color: #e9f2f3;
}
.wordcloud {
  position: relative;
  margin: 120px auto;
  width: 50%;
  height: 90%;
  // background-color: #e9f2f3;
}
.topleft {
  display: flex;
  flex-direction: row;
}
.topright {
  display: flex;
  flex-direction: row;
}
.item {
  position: relative;
  z-index: inherit;
  width: 100px;
  height: 30px;
}

.el-button {
  color: #10e7f8;
  font-size: 18px;
  font-weight: bold;
  background-color: transparent;
  border: 1px solid transparent;
}

.el-button:hover,
.el-button:focus {
  background-color: transparent;
  color: #d5d7dc;
  border: 1px solid transparent;
}

.home {
  width: 100vw;
  // height: 100vh;
  background-image: url("../assets/screen/bg2.jpg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  .el-input__inner{
        border-radius:20px;
        color: #24E0D2;
        border: solid 1px #24E0D2;
        height: 32px;
        background-color: transparent;
      }
     input::-webkit-input-placeholder {
      color: #24E0D2;
      }
  .home_content {
    width: 100%;
    display: flex;
    justify-content: space-around;
    .content_left {
      width: 40%;
      height: 100vh;
      .news_index {
        margin-top: 30px;
        margin-left: 20px;
      }
    }
    .content_center {
      width: 45%;

      .news_num {
        width: 40%;
        margin-top: 25px;
        z-index: 1000;
      }
    }
    .contetn_right {
      width: 35%;
      margin-left: -30px;
      display: flex;
      flex-direction: column;
      margin-right: 60px;
      .warn {
        position: relative;
        width: 85%;
        height: 30%;
        padding: 30px;
         background: url('../assets/screen/border.png') no-repeat;
         background-size: 100% 100%;
        cursor: pointer;
      }
      .warnImg {
        position: absolute;
        // right: 150px;
        margin-top: 50px;
        margin-left: 20px;
        
        color: white;
        font-size: 20px;
        img {
          margin-top: 10px;
          width: 80px;
        }
        .content {
          width: 280px;
          font-size: 13px;
          margin-top: -80px;
          margin-left: 100px;
        }
      }
      .text_analyse {
        background: url("../assets/screen/emotion.png") no-repeat;
        background-size: 100% 100%;
        margin-top: -25px;
        width: 100%;
        height: 400px;
      }
    }
  }
}
.change_page {
  width: 100%;
  position: absolute;
  bottom: -180px;
  transition-property: bottom;
  transition-duration: 1s;
  z-index: 999;
}
.change_page:hover {
  bottom: 20px;
}
.news_map {
 
  width: 100%;
  height: 100%;
//   padding-top: 60px;
  position: absolute;
}

</style>
